form{
	display: flex;
	align-items: center;
	justify-content: center;
}

input, button, input:focus, button:focus{
	padding: 10px;
	border-radius: 30px;
	margin: 10px;
	width: 40vw;
	align-items: center;
	border-style: none;
	outline-width: 0;
}

button, button:focus{
	width: 7vw;
}